.main-container {
  height: calc(100vh - 100px);
  width: 100%;
  overflow: hidden; }

.list-style {
  height: calc(100vh - 155px);
  overflow-y: auto;
  padding: 0;
  margin: 0;
  list-style-type: none; }

.list-style::-webkit-scrollbar {
  width: 6px; }

.list-style::-webkit-scrollbar-thumb {
  background-color: rgba(149, 149, 149, 0.8);
  border-radius: 24px; }

div.cols-title {
  padding: 0 1em;
  background-color: #f5f5f5;
  color: #999999;
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: 1px; }



