.error-exit {
  position: absolute;
  top: 0.8em;
  right: 1.5em;
  cursor: pointer; }

.error-hidden {
  display: none; }

.error-container {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  padding: 0.8em 0 0.5em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  color: #f9f9f9;
  background: #dd5a5a;
  -webkit-animation: fade-in 0.3s ease-in forwards;
  animation: fade-in 0.3s ease-in forwards; }

@keyframes fade-in {
  0% {
    top: -30px;
    opacity: 0.1; }
  15% {
    top: -20px;
    opacity: 0.2; }
  30% {
    top: -10px;
    opacity: 0.4; }
  50% {
    top: 0;
    opacity: 0.6; }
  100% {
    top: 0px;
    opacity: 1; } }

@-webkit-keyframes fade-in {
  0% {
    top: -30px;
    opacity: 0.1; }
  15% {
    top: -20px;
    opacity: 0.2; }
  30% {
    top: -10px;
    opacity: 0.4; }
  50% {
    top: 0;
    opacity: 0.6; }
  100% {
    top: 0px;
    opacity: 1; } }



